@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
.title-centered {
  text-align: center;
}

body,
html {
  margin: 0 !important;
  font-family: "Poppins", sans-serif;
}

.selected-stamps {
  border: 2px solid green;
}

.stamps {
  border-radius: 10px;
  margin: 0px 5px 0px 5px;
  cursor: pointer;
}

.stamps-container {
  padding-top: 0.875rem;
}

.toggle-export[type="checkbox"] {
  position: relative;
  width: 40px;
  height: 20px;
  -webkit-appearance: none;
  appearance: none;
  background: red;
  outline: none;
  border-radius: 2rem;
  cursor: pointer;
  box-shadow: inset 0 0 5px rgb(0 0 0 / 50%);
}

.toggle-export[type="checkbox"]::before {
  content: "";
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.5s;
}

.toggle-export[type="checkbox"]:checked::before {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  background: #fff;
}

.toggle-export[type="checkbox"]:checked {
  background: #00ed64;
}

.menu-btn {
  margin: 2rem;
}

.ahashakeheartache {
  animation: kf_shake 0.4s 1 linear;
  -webkit-animation: kf_shake 0.4s 1 linear;
  -moz-animation: kf_shake 0.4s 1 linear;
  -o-animation: kf_shake 0.4s 1 linear;
}
@-webkit-keyframes kf_shake {
  0% {
    -webkit-transform: translate(30px);
  }
  20% {
    -webkit-transform: translate(-30px);
  }
  40% {
    -webkit-transform: translate(15px);
  }
  60% {
    -webkit-transform: translate(-15px);
  }
  80% {
    -webkit-transform: translate(8px);
  }
  100% {
    -webkit-transform: translate(0px);
  }
}
@keyframes kf_shake {
  0% {
    -webkit-transform: translate(30px);
  }
  20% {
    -webkit-transform: translate(-30px);
  }
  40% {
    -webkit-transform: translate(15px);
  }
  60% {
    -webkit-transform: translate(-15px);
  }
  80% {
    -webkit-transform: translate(8px);
  }
  100% {
    -webkit-transform: translate(0px);
  }
}

.modal-container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  width: 100%;
}

.modal-header {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

.modal-content {
  text-align: center;
}

.rewardSubmitButton {
  background-color: red;
  margin-top: 10px !important;
}

/* Adjust these styles to match your desired appearance */

/* Close button styles */
.MuiIconButton-root {
  margin-right: -10px;
  margin-top: -10px;
  color: #999;
  &:hover {
    color: #333;
  }
}

/* TextField styles */
.MuiTextField-root {
  margin-bottom: 20px;
}

/* Submit button styles */
.MuiButton-root {
  background-color: #007bff;
  color: white;
  &:hover {
    background-color: #0056b3;
  }
}

.modal-buttons {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

/* Style for the "Yes" button */
.modal-buttons .MuiButton-containedSecondary {
  background-color: red;
  color: white;
  margin-right: 10px;
}

/* Hover style for the "Yes" button */
.modal-buttons .MuiButton-containedSecondary:hover {
  background-color: #ff0000;
}

/* Style for the "No" button */
/* .modal-buttons .MuiButton-containedPrimary {
  background-color: blue;
  color: white;
} */

/* Hover style for the "No" button */
.modal-buttons .MuiButton-containedPrimary:hover {
  background-color: #0000ff;
}

.ql-container {
  min-height: 200px;
  border-radius: 0 0 20px 20px;
}
.ql-toolbar.ql-snow {
  border-radius: 20px 20px 0 0;
}
.darkbg .ql-container.ql-snow,
.darkbg .ql-toolbar.ql-snow,
.darkbg .ql-snow .ql-tooltip input[type="text"] {
  border-color: rgba(255, 255, 255, 0.2);
}

.darkbg .ql-snow .ql-picker:not(.ql-color-picker):not(.ql-icon-picker) svg,
.darkbg .ql-snow.ql-toolbar button svg,
.ql-snow .ql-toolbar button svg {
  -webkit-filter: brightness(0) invert(1);
          filter: brightness(0) invert(1);
}

.darkbg .ql-snow .ql-picker,
.darkbg .ql-snow .ql-tooltip input[type="text"] {
  color: white;
}

.darkbg .ql-snow .ql-picker-options,
.darkbg .ql-snow .ql-tooltip,
.darkbg .ql-snow .ql-tooltip input[type="text"] {
  background-color: #484b52;
}

.darkbg .ql-snow .ql-tooltip[data-mode="link"]::before {
  color: white;
}

.ql-editor,
.ql-snow * {
  font-family: "DM Sans", sans-serif;
  font-size: 16px;
}

.ql-editor.ql-blank::before {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: #777e89;
}

.ql-container.ql-snow {
  border: 0;
}

.ql-toolbar.ql-snow {
  border-width: 0 0 1px 0;
}

.ql-toolbar.ql-snow .ql-picker-label,
.ql-toolbar.ql-snow .ql-picker-options {
  border: 0;
}

.ql-snow .ql-picker.ql-expanded .ql-picker-options {
  top: 33px;
}

.ql-snow .ql-picker-options {
  padding: 5px 19px;
  box-shadow: 0 15px 20px rgb(0 0 0 / 12%);
  border-radius: 9px;
}

.ql-snow .ql-picker.ql-expanded .ql-picker-label {
  color: inherit;
}

.ql-snow .ql-tooltip {
  left: 0 !important;
  border: 0;
  box-shadow: 0 5px 15px rgb(0 0 0 / 12%);
  border-radius: 5px;
}
.ql-snow .ql-tooltip input[type="text"] {
  height: auto;
  padding: 5px 8px;
  border-radius: 5px;
  outline: 0;
}

