@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

.title-centered {
  text-align: center;
}

body,
html {
  margin: 0 !important;
  font-family: "Poppins", sans-serif;
}

.selected-stamps {
  border: 2px solid green;
}

.stamps {
  border-radius: 10px;
  margin: 0px 5px 0px 5px;
  cursor: pointer;
}

.stamps-container {
  padding-top: 0.875rem;
}

.toggle-export[type="checkbox"] {
  position: relative;
  width: 40px;
  height: 20px;
  -webkit-appearance: none;
  appearance: none;
  background: red;
  outline: none;
  border-radius: 2rem;
  cursor: pointer;
  box-shadow: inset 0 0 5px rgb(0 0 0 / 50%);
}

.toggle-export[type="checkbox"]::before {
  content: "";
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.5s;
}

.toggle-export[type="checkbox"]:checked::before {
  transform: translateX(100%);
  background: #fff;
}

.toggle-export[type="checkbox"]:checked {
  background: #00ed64;
}
