.modal-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  width: 100%;
}

.modal-header {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

.modal-content {
  text-align: center;
}

.rewardSubmitButton {
  background-color: red;
  margin-top: 10px !important;
}

/* Adjust these styles to match your desired appearance */

/* Close button styles */
.MuiIconButton-root {
  margin-right: -10px;
  margin-top: -10px;
  color: #999;
  &:hover {
    color: #333;
  }
}

/* TextField styles */
.MuiTextField-root {
  margin-bottom: 20px;
}

/* Submit button styles */
.MuiButton-root {
  background-color: #007bff;
  color: white;
  &:hover {
    background-color: #0056b3;
  }
}

.modal-buttons {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

/* Style for the "Yes" button */
.modal-buttons .MuiButton-containedSecondary {
  background-color: red;
  color: white;
  margin-right: 10px;
}

/* Hover style for the "Yes" button */
.modal-buttons .MuiButton-containedSecondary:hover {
  background-color: #ff0000;
}

/* Style for the "No" button */
/* .modal-buttons .MuiButton-containedPrimary {
  background-color: blue;
  color: white;
} */

/* Hover style for the "No" button */
.modal-buttons .MuiButton-containedPrimary:hover {
  background-color: #0000ff;
}
